#starshine {
  position: absolute;
  top: 13%;
  left: 40%;
  width: 48%;
  height: 52%;
  z-index: 1;
  overflow: hidden;
  pointer-events: none;

  @include for-size(tablet) {
    top: 13%;
    left: 49%;
    width: 15%;
    height: 50%;
  }

  @include for-size(desktop) {
    top: 17%;
    height: 67%;
    left: 47%;
    width: 18%;
  }

  @media (max-width: 1199px) {
    display: none;
  }
}

.shine.first {
  top: 5%;
  left: 2%;
}

.shine.second {
  top: 2%;
  left: 62%;
}

.shine.third {
  top: -11px;
  left: 47%;
}

.shine.fourth {
  top: 24%;
  left: 11%;
}

.shine.fifth {
  top: 30%;
  left: 62%;
}

.shine.sixth {
  top: 65%;
  left: 74%;
}

.shine.seventh {
  top: 78%;
  left: 70%;
}

.shine.eighth {
  top: 86%;
  left: 20%;
}

.shine.ninenth {
  top: 50%;
  left: 50%;
}

.shine.tenth {
  top: 65%;
  left: 20%;
}

.shine.eleventh {
  top: 53%;
  left: 8%;
}

.shine {
  display: block;
  position: absolute;
  width: 200px;
  height: 300px;
  top: 50%;
  left: 50%;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  overflow: hidden;
  z-index: 2;
  color: tansparent;
  -moz-opacity: 0;
  opacity: 0;
  animation: glitter 6s linear 0s infinite normal;
  -webkit-animation: glitter 6s linear 0s infinite normal;
  -moz-animation: glitter 8s linear 0s infinite normal;
  -ms-animation: glitter 8s linear 0s infinite normal;
  -o-animation: glitter 8s linear 0s infinite normal;
}

.shine.small {
  width: 20px;
  height: 20px;
}

.shine.medium {
  width: 30px;
  height: 30px;
}

.shine.large {
  width: 50px;
  height: 50px;
}

/*CSS3 keyframes for glittering effect*/
@keyframes glitter {
  0% {
    transform: scale(0.3) rotate(0deg);
    opacity: 0;
  }
  25% {
    transform: scale(1) rotate(360deg);
    opacity: 1;
  }
  50% {
    transform: scale(0.3) rotate(720deg);
    opacity: 0;
  }
  100% {
    transform: scale(0.3) rotate(0deg);
    opacity: 0;
  }
}
